@font-face {
  font-family: 'Neuzeit Grotesk';
  src: url('assets/fonts/neuzegrotreg-webfont.eot');
  src: url('assets/fonts/neuzegrotreg-webfont.eot?#iefix') format('embedded-opentype'),
  url('assets/fonts/neuzegrotreg-webfont.woff2') format('woff2'),
  url('assets/fonts/neuzegrotreg-webfont.woff') format('woff'),
  url('assets/fonts/neuzegrotreg-webfont.ttf') format('truetype'),
  url('assets/fonts/neuzegrotreg-webfont.svg#neuzeit_grotesk_tregular') format('svg');
  font-weight: 400;
  font-style: normal;
}

